import { NextRequest, NextResponse } from 'next/server';
import { getAllProjects, saveAllProjects, getProjectById, ProjectProduct } from '@/lib/projects';

export async function GET(req: NextRequest, { params }: { params: { id: string } }) {
  const id = Number(params.id);
  const project = getProjectById(id);
  if (!project) return NextResponse.json({ error: 'Proje bulunamadı' }, { status: 404 });
  return NextResponse.json(project);
}

export async function PUT(req: NextRequest, { params }: { params: { id: string } }) {
  const id = Number(params.id);
  const { name, products } = await req.json();
  if (!name || !Array.isArray(products)) {
    return NextResponse.json({ error: 'Eksik veri' }, { status: 400 });
  }
  const projects = getAllProjects();
  const idx = projects.findIndex(p => p.id === id);
  if (idx === -1) return NextResponse.json({ error: 'Proje bulunamadı' }, { status: 404 });
  projects[idx] = { ...projects[idx], name, products: products as ProjectProduct[] };
  saveAllProjects(projects);
  return NextResponse.json(projects[idx]);
}

export async function DELETE(req: NextRequest, { params }: { params: { id: string } }) {
  const id = Number(params.id);
  const projects = getAllProjects();
  const idx = projects.findIndex(p => p.id === id);
  if (idx === -1) return NextResponse.json({ error: 'Proje bulunamadı' }, { status: 404 });
  const deleted = projects.splice(idx, 1);
  saveAllProjects(projects);
  return NextResponse.json(deleted[0]);
}
